s de cada opción y puntuarlas en función de la ponderación previa; de esta manera se “califica” cada criterio otorgándole un valor, que puede ser comparado con los de otras opciones listadas.")])])]),_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"6"}},[_c('div',{staticClass:"col-md-11 col-12 mb-4 mb-md-0"},[_c('h4',[_vm._v("Elección de la opción")]),_c('p',[_vm._v("Una vez se han cuantificado todos los criterios, solo resta hacer la suma de todas las valoraciones obtenidas por cada alternativa y elegir aquella con el puntaje conjunto más elevado. De esta forma se conserva la objetividad, al dejar la elección condicionada a una operación aritmética. ")])])])])],1),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"flip-left"}},[_vm._v("Se presenta el siguiente ejemplo para comprender su aplicabilidad: ")]),_vm._m(3),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"flip-left"}},[_vm._v("Existen otros sistemas de priorización utilizados; pero, su eje fundamental opera bajo el mismo sistema, es decir, determinar qué es lo más importante a través de una serie de criterios que contemplan tiempo, recursos, efectividad y esfuerzo. A continuación, una breve reseña de algunos de ellos:")]),_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta color-primario","data-aos":"flip-left"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Pirámide de priorización"}},[_c('div',{staticClass:"col-md-11 col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Una pirámide es una estructura con una base ancha, con mayor capacidad para contener actividades y soporta los niveles medio y superior, por lo que la priorización funciona en este sentido. En la cúspide se ubican las tareas o actividades más importantes que requieren especial atención; en el nivel intermedio aquellas que no se deben realizar tan pronto, pero que igual tienen importancia. En la base se ubican las tareas que no son imprescindibles y que, por lo tanto, si la situación durante el desa